
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'



import api from "../../lib/api";


import storage from "../../utile/storage";

export default {
components: {
XHeader,
Cell,
Group,
CellBox
},
created() {


},
mounted() {
this.getProjectList();
},

data() {
return {
businessIndex: '',
showContent: false,
testUser: null,
project: {
currentList: []
}
};
},
methods: {
testLogin() {
this.testUser = storage.get("_njLoginInfo").user_info;
},

/**
* 返回Native
*/
_htmlBackToNative() {
switch (this.$store.state.onLogin.terminal_name) {
case "ios":
window.htmlBackToNative();
break;
case "android":
break;
window.android.htmlBackToNative();
default:
break;
}
},

/**
* 获取项目分类列表
*/
getProjectList() {
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/sys/dict/menu";
const domainUrl = this.$store.state.domainUrl;
let propsData = {
type: "act_category"
};
api.major(url, access_token, propsData).then(res => {
console.log("res:", res);
switch (res.state) {
case 10001:
this.project.currentList = res.data;
let projectListitem = this.project.currentList;
projectListitem.forEach(items => {
items.list && items.list.forEach(item => {

item.icon = domainUrl + item.icon;
})
})
break;

default:
break;
}
});
},

/**
* 展开子业务列表
*/
openChildBusinessList(item, index) {
if(item.indexId !== index) {
this.$set(item, 'indexId', index);

} else {
this.$set(item, 'indexId', '');

}

console.log('item:', item);

},

/**
* router start
*/
routeToLink(path) {
path = path || "/";
this.$router.push({
path: path
});
},

/**
* router end
*/
major() {
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/sys/perselect/group";
console.log("_njLoginInfo:", access_token);
let propsData = {


};
api.major(url, access_token, propsData).then(res => {
console.log("res:", res);
});
},

login() {
if (this.$store.state.onLogin.length) {
return;
}

api.login()
.then(res => {
if (!res) {
return;
}
if (storage.get("_njLoginInfo")) {
localStorage.clear();
}
console.log("res:", res);
let njLoginInfo = res;
storage.set("_njLoginInfo", njLoginInfo);
})
.then(res => {
console.log(
'storage.get("_njLoginInfo"):',
storage.get("_njLoginInfo")
);
});
}
}
};
