
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'



import api from "../../lib/api";


import storage from "../../utile/storage";


import M_ApprovalList from "../../mixins/approval/approvalList";

export default {
mixins: [M_ApprovalList],
components: {
XHeader,
Search,
Tab,
TabItem,
Loading
},
data() {
return {
approvalStateType: "todo", //导航栏名称
/**
* 待审批
*/
todo: {
currentPage: 1,
currentList: [],
noCurrentData: false,
noCurrentDataMore: false,
refreshState: false,
filterState: false,
searchState: false,
currentTotal: null,
status: ""
},
/**
* 已审批
*/
handled: {
currentPage: 1,
currentList: [],
noCurrentData: false,
noCurrentDataMore: false,
refreshState: false,
filterState: false,
searchState: false,
currentTotal: null,
status: ""
}
};
},
created() {
this.resetCurrentData(this.todo);
this.resetCurrentData(this.handled);
},
mounted() {
this.getCurrentList(this.todo);
},
methods: {
/**
* 搜索
*/
currentSearch() {
this.showSearch = true;
},

/**
* 审批状态切换 start
*/
changeProcessing(stateType) {
this.showSearch = false;
this.showfilter = false;
switch (stateType) {

case "todo":
this.showSearch = false;
this.approvalStateType = "todo";
if (this.todo.refreshState) {
return;
}
if (!this.todo.currentList.length) {
this.resetCurrentData(this.todo);
this.getCurrentList(this.todo);
}
console.log(
"this.approvalStateType:",
this.approvalStateType
);
break;


case "handled":
this.approvalStateType = "handled";
if (this.handled.refreshState) {
return;
}
if (!this.handled.currentList.length) {
this.resetCurrentData(this.handled);
this.getCurrentList(this.handled);
}
break;
default:
break;
}
},

/**
* 私有路由
*/
_routeToLink(path, query) {
let _query = {
source: query && this.$route.name,
taskName: query && query.taskName,
taskId: query && query.taskId,
status: query && query.status,
formKey: query && query.formKey
};
this.routeToLink(path, _query);
}
}
};
