
// css
import 'normalize.css';
import './main.styl';

// 公共样式
import './assets/css/public.styl';

// vux样式覆盖
import './assets/css/vux/cover.less';

import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';


// storage
import storage from "./utile/storage";

// 上拉加载、下拉刷新插件
import VueScroller from 'vue-scroller';

Vue.use(VueScroller);



Vue.config.productionTip = false;


router.beforeEach((to, from, next) => {
  next();

  document.title = to.meta.title || '暂无标题';
});

/**
 * navtveLogin
 */
window['nativeToHtmlLogin'] = function (terminalName) {
  let njLoginInfo = {};
  localStorage.clear();
  store.state.onLogin = (terminalName && JSON.parse(terminalName)) || {};
  if (!!store.state.onLogin['access_token']) {
    store.state.onLogin['access_token'] && !store.state.onLogin['access_token'].id && (store.state.onLogin['access_token'].id = store.state.onLogin['access_token'].access_tokenID);
  } else {
    return store.state.onLogin;
  }
  
  njLoginInfo = store.state.onLogin;
  storage.set("_njLoginInfo", njLoginInfo);

  return store.state.onLogin;
}

window['nativeToHtmlSelection'] = function (selectionList) {
  selectionList = JSON.parse(selectionList) || {};
  switch (selectionList.selection_name) {
    case 'assignee':
      store.state.processList.assignee = selectionList.data;
      break;
    case 'joint':
      store.state.processList.joint = selectionList.data;
      break;
    case 'leader':
      store.state.processList.leader = selectionList.data;
      break;
    case 'cC':
      store.state.processList.cC = selectionList.data;
      break;
    case 'forward':
      store.state.forwardList = selectionList.data;
      break;

    default:
      break;
  }
  return store.state.processList;
}


new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
