
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'



import api from "../../lib/api";


import storage from "../../utile/storage";

export default {
components: {
XHeader,
Search,
Group,
Checklist,
Cell,
Divider,
XButton,
CellBox,
Loading
},
data() {
return {
showGroupList: true,
showGroupTitle: false,
groupTitle: '',
showLoading: false,
noNetwork: false,
selecgState: true,
contactType: "often",
checkedAll: false,
currentList: {
personnel: [],
group: []
}
};
},
created() {},
mounted() {
this.renderSelectState();
this.getpersonnelList(this.contactType);
},
computed: {},
methods: {
contactTypeBtn(contactType) {
switch (contactType) {
case "often":
this.contactType = "often";
this.getpersonnelList(this.contactType);
break;
case "group":
this.contactType = "group";
this.getGroupList();
break;
case "department":
this.contactType = "department";
break;
case "all":
this.contactType = "all";
break;

default:
break;
}
},

/**
* 刷新页面
*/
btnRefresh() {
this.getpersonnelList(this.contactType);
},

selectGroup(contactType, group) {
this.showGroupList = false;
this.showGroupTitle = true;
this.groupTitle = group.name;
let groupId = group.id;
this.getpersonnelList(contactType, groupId)
},

backGroupList() {
this.showGroupList = true;
this.showGroupTitle = false;
this.groupTitle = '';
},

/**
* 返回上一页
*/
routeToBack() {
let personnelList = this.currentList.personnel;
personnelList &&
personnelList.forEach(item => {
item._checked = false;
});
this.$router.back();
},

/**
* 重置选择框
*/
renderSelectState() {
let personnel_type = this.$route.query.personnel_type;
if (personnel_type == "department" || personnel_type == "leader") {
this.selecgState = false;
} else {
this.selecgState = true;
}
},

/**
* 重置已选择的人员列表
*/
resetProcessList() {
switch (this.$route.query.personnel_type) {

case "copyCC":
this.$store.state.copyList.cC = [];
break;
case "joint":
this.$store.state.processList.joint = [];
break;
case "cC":
this.$store.state.processList.cC = [];
break;
default:
break;
}
},

/**
* 提交选择的人员
*/
submitPersonnelList() {
this.resetProcessList();
let personnelList = this.currentList.personnel;
for (let index in personnelList) {
let item = personnelList[index];
if (!item._checked) {
continue;
}
/*
*选择类型判断:
forwardLis: 转发
joint: 会签
cC: 抄送
*/
switch (this.$route.query.personnel_type) {
case "copyCC":
this.$store.state.forwardLis.push(item);
break;
case "joint":
this.$store.state.processList.joint.push(item);
break;
case "cC":
this.$store.state.processList.cC.push(item);
break;
default:
break;
}
}
console.log(
"this.$store.state.processList:",
this.$store.state.processList
);
this.currentList.personnel.forEach(item => {
item._checked = false;
});

this.$router.back();
},

/**
* 人员全选
*/
toggleAll() {
this.currentList.personnel.forEach(item => {
item._checked = !this.checkedAll;
});
},

/**
* 获取人员列表
*/
getpersonnelList(contactType, groupId) {
this.showLoading = true;
this.noNetwork = false;
groupId = groupId || "";
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "";

switch (contactType) {
case "often":
url = "rest/sys/perselect/commonly";
break;
case "group":
url = "rest/sys/perselect/groupUser";
break;

default:
break;
}
let propsData = {
id: groupId
};
console.log("propsData:", propsData);

api.major(url, access_token, propsData)
.then(res => {
console.log("res:", res);
switch (res.state) {
case 10001:
this.currentList.personnel = res.data;
break;

default:
break;
}
this.showLoading = false;
})
.catch(error => {
this.showLoading = false;
this.noNetwork = true;
});
},

/**
* 获取群组列表
*/
getGroupList() {
this.showLoading = true;
this.noNetwork = false;
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/sys/perselect/group";
let propsData = {};
api.major(url, access_token, propsData)
.then(res => {
console.log("res:", res);

switch (res.state) {
case 10001:
this.currentList.group = res.data;
break;

default:
break;
}
this.showLoading = false;
})
.catch(error => {
this.showLoading = false;
this.noNetwork = true;
});
}
}
};
