// api {login, major, ...}
import api from "../../lib/api";

// storage
import storage from "../../utile/storage";

export default {
    components: {
       
    },
    data() {
        return {
            personal: {
                currentList: [],
            }
        };
    },
    created() {
    },
    mounted() {
        this.getPersonalDesc();
    },
    methods: {
        getPersonalDesc() {
            const url = "rest/oa/contact/get";
            let access_token = this.$route.query.__sid;
            let propsData = {
                id: this.$route.query.id
            }
            api.postMajorAll(url, access_token, propsData).then(res => {
                console.log('res:', res);
                switch (res.state) {
                    case 10001:
                        console.log('1:', 1);
                        this.personal.currentList = res.data;
                        break;
                
                    default:
                        break;
                }

                
            }).catch(error => {
                console.log('error:', error);
                
            })
        }
    }
};
