
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'



import api from "../../lib/api";


import storage from "../../utile/storage";

export default {
components: {
XHeader,
Group,
CellBox,
Search,
Loading
},
data() {
return {
noNetwork: false,
showLoading: false,
contractList: [],
testLogin: {}
};
},
created() {
this.getContractList();
},
methods: {
/**
* 重新加载页面
*/
btnRefresh() {
this.contractList = [];
this.getContractList();
},
/**
* 下拉刷新
*/
refresh(done) {
this.contractList = [];
this.getContractList().then(res => {
done();
console.log("下拉刷新:");
});
},

getContractList() {
this.showLoading = true;
this.noNetwork = false;
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/cm/contract/list";
let propsData = {
pageNo: "1",
pageSize: "10"
};
return api.major(url, access_token, propsData).then(res => {
console.log("res:", res);

switch (res.state) {
case 10001:

this.contractList = res.data;
break;

default:
break;
}
this.showLoading = false;

}).catch(error => {
console.log('error:', error);
this.noNetwork = true;
this.showLoading = false;
console.log('this.showLoading:', this.showLoading);

})
},

getContractItem(contractInfo) {
this.$store.state.contract = contractInfo;
this.$router.push({
path: "/newApply"
});
}
}
};
