
// storage
import storage from "../../utile/storage";

// api {login, major, ...}
import api from "../../lib/api";

export default {
    data() {
        return {
            showLoading: false,
            searchHeight: 58,
            noNetwork: false, //网络状态
            showSearch: false,
            showFilter: false,
            searchResult: false,
            filterResult: false,
            filterList: [],
            filterValue: null,
            searchValue: null,

        };
    },
    mounted() {
        this.$refs.myscroller.finishInfinite(true);
    },
    methods: {
        /**
         * 搜索
         */
        searchType(item) {
            let stateType = null;
            //判断待办和已办
            switch (this.approvalStateType) {
                case "todo":
                    stateType = this.todo;
                    break;

                case "handled":
                    stateType = this.handled;
                    break;

                case "initiated":
                    stateType = this.initiated;
                    break;

                case "copy":
                    stateType = this.copy;
                    break;

                default:
                    break;
            }
            console.log("stateType:", stateType);
            this.resetCurrentData(stateType);
            stateType.searchState = true;
            this.getCurrentList(stateType);
            this.showFilter = false;
        },

        /**
         * 筛选
         */
        filterType(item) {
            let stateType = null;
            //判断待办和已办
            switch (this.approvalStateType) {
                case "todo":
                    stateType = this.todo;
                    stateType.status = "";
                    break;

                case "handled":
                    stateType = this.handled;
                    stateType.status = "expired";
                    break;

                case "initiated":
                    stateType = this.initiated;
                    break;

                case "copy":
                    stateType = this.copy;
                    stateType.status = "copy";
                    break;

                default:
                    break;
            }
            console.log("stateType:", stateType);
            this.resetCurrentData(stateType);
            this.filterValue = item.value;
            stateType.filterState = true;
            
            this.getCurrentList(stateType);
            this.showFilter = false;
        },

        /**
         * 获取筛选列表
         */
        getFilterList() {
            if (this.filterList.length > 0) {
                return;
            }

            let access_token = storage.get("_njLoginInfo").access_token.id;
            let url = "rest/sys/dict/list";
            let propsData = {
                type: "act_category"
            };

            api.major(url, access_token, propsData)
                .then(res => {
                    console.log("res:", res);

                    switch (res.state) {
                        case 10001:
                            this.filterList = res.data;
                            break;
                        case 10002:
                            break;

                        default:
                            break;
                    }
                })
                .catch(error => {
                    this.noNetwork = true;
                });
        },

        /**
         * targetSearchBar 打开搜索框
         */
        targetSearch() {
            this.showSearch = !this.showSearch;
            this.showFilter = false;
        },

        /**
         * targetfilter 打开筛选
         */
        targetFilter() {
            this.showFilter = !this.showFilter;
            this.showSearch = false;
            this.getFilterList();
        },

        /**
         *下拉刷新及上拉加载
         */
        // 下拉刷新
        refresh(done) {
            this.filterState = false;
            this.searchState = false;
            let stateType = null;
            let stateTypeName = null;
            switch (this.approvalStateType) {
                case "todo":
                    stateType = this.todo;
                    stateTypeName = "todo";
                    break;
                case "handled":
                    stateType = this.handled;
                    stateTypeName = "handled";
                    break;
                case "initiated":
                    stateType = this.initiated;
                    stateTypeName = "initiated";
                    break;
                case "copy":
                    stateType = this.copy;
                    stateTypeName = "copy";
                    break;
                default:
                    break;
            }
            if (this.noNetwork || stateType.noCurrentData) {
                this.resetCurrentData(stateType);
                return;
            }
            this.resetCurrentData(stateType);
            this.getCurrentList(stateType).then(res => {
                done();
            });
            console.log("下拉刷新", stateTypeName);
        },
        // 上拉加载
        infinite(done) {
            let stateType = null;
            let stateTypeName = null;
            switch (this.approvalStateType) {
                case "todo":
                    stateType = this.todo;
                    stateTypeName = "todo";
                    break;
                case "handled":
                    stateType = this.handled;
                    stateTypeName = "handled";
                    break;
                case "initiated":
                    stateType = this.initiated;
                    stateTypeName = "initiated";
                    break;
                case "copy":
                    stateType = this.copy;
                    stateTypeName = "copy";
                    break;
                default:
                    break;
            }
            console.log("stateType.noCurrentData:", stateType.noCurrentData);

            if (this.noNetwork || stateType.noCurrentData) {
                this.resetCurrentData(stateType);
                return;
            }
            if (stateType.noCurrentDataMore) {
                done(true);
                return;
            } else {
                stateType.currentPage++;
                this.getCurrentList(stateType).then(res => {
                    console.log("上拉加载更多", stateTypeName);
                    done();
                });
            }
        },

        /**
         * 获取数据列表
         */
        getCurrentList(stateType) {
            this.showLoading = true;
            this.noNetwork = false;
            let access_token = storage.get("_njLoginInfo").access_token.id;
            let url = null;
                switch (stateType) {
                    case this.todo:
                        url = "rest/sys/affair/todo";
                        break;
                    case this.handled:
                        if (stateType.filterState) {
                        url = "rest/sys/affair/filter";
                        } else if(stateType.searchState) {
                            url = "rest/sys/affair/find";
                            } else {
                                url = "rest/sys/affair/handled";
                            }
                        break;
                    case this.initiated:
                        url = "rest/sys/affair/draft";
                        break;
                    case this.copy:
                        url = "rest/sys/affair/copy";
                        break;
                    default:
                        break;
                }

            stateType.refreshState = true;
            let propsData = {
                pageSize: 10,
                pageNo: stateType.currentPage,
                formKey: this.filterValue,
                status: stateType.status,
                title: this.searchValue
            };
            console.log("_njLoginInfo:", access_token);
            console.log('url:', url);
            console.log('propsData:', propsData);
            
            return api
                .major(url, access_token, propsData)
                .then(res => {
                    console.log("res:", res);
                    switch (res.state) {
                        case 10001:
                            if (stateType.currentPage == 1) {
                                stateType.currentList = res.data;
                                stateType.currentTotal =
                                    stateType.currentList.length;
                                console.log(
                                    "stateType.currentList:",
                                    stateType.currentList
                                );
                            } else {
                                stateType.currentList = stateType.currentList.concat(
                                    res.data
                                );
                                stateType.currentTotal =
                                    stateType.currentList.length;
                                console.log(
                                    "stateType.currentList:",
                                    stateType.currentList
                                );
                            }
                            break;
                        case 10002: //没有更多数据
                            stateType.noCurrentDataMore = true;
                            if (stateType.currentList.length == 0) {
                                stateType.noCurrentData = true;
                            }
                            break;
                        default:
                            break;
                    }
                    this.showLoading = false;
                    stateType.refreshState = false;
                })
                .catch(error => {
                    console.log('error:', error);
                    
                    this.noNetwork = true;
                    this.showLoading = false;
                    console.log('this.noNetwork:', this.noNetwork);
                    
                });
        },

        /**
         * 重置当前数据列表
         */
        resetCurrentData(stateType) {
            stateType.currentPage = 1;
            stateType.currentList = [];
            stateType.noCurrentData = false;
            stateType.noCurrentDataMore = false;
            stateType.refreshState = false;
            stateType.filterState = false;
            stateType.searchState = false;
            stateType.currentTotal = null;
            stateType.status = "";
        },

        /**
         * 按钮刷新
         */
        btnRefresh() {
            let stateType = null;
            let stateTypeName = null;
            switch (this.approvalStateType) {
                case "todo":
                    stateType = this.todo;
                    stateTypeName = "todo";
                    break;
                case "handled":
                    stateType = this.handled;
                    stateTypeName = "handled";
                    break;
                case "initiated":
                    stateType = this.initiated;
                    stateTypeName = "initiated";
                    break;
                case "copy":
                    stateType = this.copy;
                    stateTypeName = "copy";
                    break;
                default:
                    break;
            }
            this.resetCurrentData(stateType);
            this.getCurrentList(stateType);
            console.log("刷新", stateTypeName);
        },

        /**
         * 路由
         */
        routeToLink(path, query) {
            this.$router.push({
                path,
                query
            });
        }
    }
};
