

import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import DatetimeRange from 'vux/src/components/datetime-range/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'



import "../../assets/css/vux/cover.less";


import TypeTitle from "../../components/approval/TypeTitle.vue";


import api from "../../lib/api";


import storage from "../../utile/storage";

import NativeInteraction from '../../mixins/nativeInteraction/nativeInteraction';

export default {
name: "newApply",
components: {
XHeader,
Group,
Cell,
XInput,
DatetimeRange,
XTextarea,
XButton,
XDialog,
Toast,
TypeTitle
},
data() {
return {
showFoward: false,
showPositionValue: false,
remindMsg: "",
processComment: "",
showAddOften: false,
oftenWordsTxt: '',
currentList: {
often: [],
myOften: []
},
forwardList: [],
};
},
mounted() {
console.log('this.$query.processTarget:', this.$route.query);

this.$route.query.processTarget == 'transfer'? this.showFoward = true : this.showFoward = false;
this.getOftenList();
},
computed: {
copyPersonnel() {
let forwardList = this.$store.state.forwardList;
let copyPersonnel = null;
forwardList && forwardList.forEach(item => {
if (copyPersonnel) {
copyPersonnel += `、${item.name}`;
} else {
copyPersonnel = item.name;
}
});
return copyPersonnel;
}
},
methods: {
randerOften(comment) {
this.processComment = comment;
},

/**
* 处理部门审核、会签、领导签发、抄送人员数据 类型：string
*/
getPersonnelList() {
let forwardList = this.$store.state.processList.forwardList;

this.forwardList = this.getPersonnelTypeString(forwardList);
},

/**
* 获取部门审核、会签、领导签发、抄送人员名单方法
*/
getPersonnelTypeString(personnelList) {
let personnelType = null;
personnelList.forEach(item => {
if (personnelType) {
personnelType += `;${item.name}(${item.num})`;
} else {
personnelType = `${item.name}(${item.num})`;
}
});
return personnelType;
},

/**
* 添加常用语
*/
addOftenWords() {
this.editOftenWords();
},

removeOftenWords(item) {
this.editOftenWords(true, item.id);
},

/**
* 添加/删除常用语方法
*/
editOftenWords(oftenDelete, id) {

oftenDelete = oftenDelete || '';
id = id || '';
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/sys/comment/save";
let propsData = {
name: this.oftenWordsTxt,
isDelRecord: oftenDelete,
id: id
}

return api.major(url, access_token, propsData).then(res => {
console.log('res:', res);

switch (res.state) {
case 10001:
this.remindMsg = res.msg;
console.log('res:', res);
break;

default:
break;
}
}).catch(error => {
console.log('error:', error);
this.noNetWork = true;
})
},

/**
* 获取我的常用语
*/
getOftenList() {
let url = "rest/sys/comment/list";

this.getCurrentList(url, 'myOften');
},

/**
* 获取公共常用语
*/
getOftenList() {
let url = "rest/sys/comment/common";

this.getCurrentList(url, 'often');
},

/**
* 获取服务器数据方法
*/
getCurrentList(url, arr, propsData) {
let access_token = storage.get("_njLoginInfo").access_token.id;

propsData = propsData || {};

return api.major(url, access_token, propsData).then(res => {
console.log('res:', res);

switch (res.state) {
case 10001:
this.currentList[arr] = res.data;
console.log('this.currentList[arr]:', this.currentList[arr]);

break;

default:
break;
}
}).catch(error => {
console.log('error:', error);
this.noNetWork = true;
})
},

/**
* 提交审批意见（完成审批操作）
*/
submit() {
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/cm/purchase/payment/apply";

let query = this.$route.query;

let propsData = {
id: query.id,
paymentMoney: query.paymentMoney,
paymentDate: query.paymentDate,
contract: {
id: query.contractId
},
processTask: query.processTask,
processTarget: query.processTarget,
processName: query.processName,
procInsId: query.procInsId,
processComment: this.processComment
};
console.log("propsData:", propsData);

return api
.major(url, access_token, propsData)
.then(res => {
console.log("res:", res);

if (res.state == 10001) {
this.remindMsg = res.msg;
this.showPositionValue = true;
this.processComment = "";
this.$router.go(-2);
}
})
.catch(res => {
console.log("出错:", res);
});
},

routerToLink(url) {
this.$router.push({
path: url
});
}
}
};
