

import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
XHeader,
Group,
Cell
},
data() {
return {

}
},
methods: {
/**
* 返回Native
*/
_htmlBackToNative() {
switch (this.$store.state.onLogin.terminal_name) {
case 'ios':
window.htmlBackToNative();
break;
case 'android':
window.android.htmlBackToNative();
break;

default:
break;
}
},

/**
* 路由
*/
routeToLink(path, query) {
query = query || {};
this.$router.push({
path,
query
})
}
}
};
