
export default {
    methods: {
        /**
         * 跳转到原生选人
         */
        _htmlToNativeSelection(selectionList) {
            let propsData = {
                selection_name: selectionList
            };
            propsData = JSON.stringify(propsData);
            alert(this.$store.state.onLogin.terminal_name);
            switch (this.$store.state.onLogin.terminal_name) {
                case "ios":
                    alert(this.$store.state.onLogin.terminal_name);
                    window.HtmlToNativeSelection(propsData);
                    break;
                case "android":
                    alert(this.$store.state.onLogin.terminal_name);
                    window.android.HtmlToNativeSelection(propsData);
                    break;
                default:
                    break;
            }
        },
        /**
         * 返回Native
         */
        _htmlBackToNative() {
            switch (this.$store.state.onLogin.terminal_name) {
                case "ios":
                    window.htmlBackToNative();
                    break;
                case "android":
                    break;
                    window.android.htmlBackToNative();
                default:
                    break;
            }
        }
    }
};
