const cmApi = "/cmApi/";
const cmsApi = "/cmsApi/";
const allApi = "/allApi/";

const headers = {

}

// 能建登入接口，只能通过url传值
const login = (userData) => {
    if (!userData) {
        userData = {
            username: '2224',
            password: '123',
            clientLogin: true,
            rememberMe: 'on'
        }
    }

    let userUrl = `?username=${userData.username}&password=${userData.password}&clientLogin=${userData.clientLogin}&rememberMe=${userData.rememberMe}`;

    let url = cmApi + 'a/login' + userUrl;
    return axios
        .post(url)
        .then(res => {
            return res.data.data;
        })
        .catch((error) => {
            console.log(error);
        });
}


/**
 * 主业务接口 Post (cmis-cm服务器)
 * @param url 
 * @param access_token 
 * @param propsData 
 */
const major = (url, access_token, propsData) => {

    // if (!access_token) {
    //     access_token = login(userData).access_token.id;
    // }

    // 线上
    url = cmApi + url + '?__sid=' + access_token;
    // url = cmApi + url;
    console.log('url :', url);

    return axios
        .post(url, propsData)
        .then(res => {
            return res.data;
        })
}

/**
 * 主业务接口 Post (cmis-all服务器)
 * @param url 
 * @param access_token 
 * @param propsData 
 */
const postMajorAll = (url, access_token, propsData) => {

    // 线上
    url = allApi + url + '?__sid=' + access_token;
    console.log('url :', url);

    return axios
        .post(url, propsData)
        .then(res => {
            return res.data;
        })
}

// 主业务接口 Get cmis-cms服务器
const getMajor = (url, access_token, propsData) => {

    // if (!access_token) {
    //     access_token = login(userData).access_token.id;
    // }

    // 线上
    let pageNum = propsData && ((propsData.pageNum && '&pageNum=' + propsData.pageNum) || ''),
        pageSize = propsData && ((propsData.pageSize && '&pageSize=' + propsData.pageSize) || ''),
        categoryId = propsData && ((propsData.categoryId && '&categoryId=') + propsData.categoryId);
    propsData = pageNum + pageSize + categoryId;
    access_token = '?__sid=' + access_token;
    url = cmsApi + url + access_token + propsData;

    console.log('url :', url);
    return axios
        .get(url)
        .then(res => {
            return res.data;
        })
}


export default {
    postMajorAll,
    major,
    getMajor,
    login,
}