

import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Loading from 'vux/src/components/loading/index.vue'



import storage from "../../../utile/storage";


import api from "../../../lib/api";

export default {
name: "newApply",
components: {
XHeader,
Tab,
TabItem,
Swiper,
Loading
},
data() {
return {
articleErrorImg: '',
swiperIndex: 0, //轮播下标
showLoading: false,
noNetwork: false,
showCarousel: true,
nav: {
currentList: []
},
carousel: {
currentList: []
},
article: {
errorImg: require('../../../assets/images/internalNews/article_default.png'),
currentList: [],
}
};
},
created() {
this.resetCurrentData(this.article);
this.getNav();
this.getCarouse();
this.getHomeArticle();
},
mounted() {
},
methods: {
/**
* 返回Native
*/
_htmlBackToNative() {
switch (this.$store.state.onLogin.terminal_name) {
case "ios":
window.htmlBackToNative();
break;
case "android":
break;
window.android.htmlBackToNative();
default:
break;
}
},

linkArticleDesc(url) {
url = 'http://dev.cmis.htpc.com.cn:8080' + url;
console.log('url:', url);
window.location.href = url;

},

/**
* 查看更多文章
*/
articleMore(category) {
let url = '/activeDesc';
let _query = {
categoryId: category.id,
categoryName: category.name
}
this.routeToLink(url, _query)
},

routeToLink(path, query) {
query = query || {};
this.$router.push({
path,
query
})
},
/**
* 获取首页文章列表
*/
getHomeArticle() {
this.showCarousel = true;
let url = `rest/cms/InternalNews/article/headPage`;
this.getArticle(url);
},

/**
* 获取导航文章列表
*/
getNavArticle(navId) {
this.showCarousel = false;
let url = `rest/cms/InternalNews/article/${navId}`;
this.getArticle(url);
},

/**
* 获取内部咨询导航
*/
getNav() {
this.showLoading = true;
this.noNetwork = false;
let url = "rest/cms/InternalNews/nav/1";
return this.getInternalNews(this.nav, url)
.then(res => {
this.showLoading = false;
})
.catch(error => {
console.log("error:", error);
this.noNetwork = true;
this.showLoading = false;
});
},

/**
* 获取轮播资源
*/
getCarouse() {
let url = "rest/cms/InternalNews/carousel";
return this.getInternalNews(this.carousel, url).then(res => {
this.carousel.currentList.forEach(item => {

item.img =
"http://dev.cmis.htpc.com.cn:8080/cmis-cms" +
item.image;
});
console.log(
"this.carousel.currentList:",
this.carousel.currentList
);
});
},


/**
* 获取文章列表方法
*/
getArticle(url, propsData) {
propsData = propsData || '';
let access_token = storage.get("_njLoginInfo").access_token.id;
return api.getMajor(url, access_token, propsData).then(res => {
console.log("articleRes:", res);
switch (res.state) {
case 10001:
this.article.currentList = res.data;
this.article.currentList.forEach(article => {
article.articleList.forEach(item => {
item.img = (item.image && 'http://dev.cmis.htpc.com.cn:8080/cmis-cms' + item.image) || this.article.errorImg;
console.log('item:', item.img);
})
})
console.log(
"this.article.currentList:",
this.article.currentList
);
break;
case 10002:
console.log('暂无数据:');

break;
default:
break;
}

}).catch(error => {
console.log('error:', error);

})
},

/**
* 获取内部资讯方法
*/
getInternalNews(stateType, url, propsData) {
let access_token = storage.get("_njLoginInfo").access_token.id;
propsData = propsData || "";
url = url;
return api.getMajor(url, access_token, propsData).then(res => {
console.log("res:", res);
switch (res.state) {
case 10001:
stateType.currentList = res.data;
break;
default:
break;
}
});
},

/**
* 按钮刷新
*/
btnRefresh() {
this.resetCurrentData(this.article);
this.resetCurrentData(this.article);
this.getNav();
this.getCarouse();
this.getHomeArticle();
},

/**
* 下拉刷新
*/
refresh(done) {
this.resetCurrentData(this.article);
this.getNav().then(res => {
this.getCarouse()
.then(res => {
this.getHomeArticle();
})
.then(res => {
done();
console.log("下拉刷新:");
});
});
},


/**
* 重置当前数据列表
*/
resetCurrentData(stateType) {
stateType.currentPage = 1;
stateType.currentList = [];
stateType.typeId = '';
},





























}
};
