
// 转换为json格式，再存储到本地
const set = (key, value) => {
    let json = JSON.stringify(value);
    return localStorage.setItem(key, json);
}


// 去除json格式，获取数据
const get = (key) => {
    let json = localStorage.getItem(key);
    return JSON.parse(json);
}


export default {
    set,
    get
}