import TabItem from 'vux/src/components/tab/tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'



import TypeTitle from "../../components/approval/TypeTitle.vue";


import api from "../../lib/api";


import storage from "../../utile/storage";


import M_ApprovalList from "../../mixins/approval/approvalList";

export default {
mixins: [M_ApprovalList],
components: {
XHeader,
Tab,
TabItem,
Loading
},
data() {
return {
approvalStateType: "copy", //导航栏名称
copy: {
currentPage: 1,
currentList: [],
noCurrentData: false,
noCurrentDataMore: false,
refreshState: false,
filterState: false,
searchState: false,
currentTotal: null,
state: ""
}
};
},
created() {
this.resetCurrentData(this.copy);
},
mounted() {
this.getCurrentList(this.copy);
},
methods: {
/**
* 私有路由
*/
_routeToLink(path, query) {
let _query = {
source: this.$route.name,
taskId: query && query.taskId,
status: query && query.state,
formKey: query && query.formKey
};
this.routeToLink(path, _query);
},
routeToLink(url, query) {
this.$router.push({
path: url,
query: {
source: this.$route.name,
taskId: query && query.id,
formKey: query && query.formKey,
state: query && query.status
}
});
}
}
};
