
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'



import api from "../../lib/api";


import storage from "../../utile/storage";


import TypeTitle from "../../components/approval/TypeTitle.vue";

export default {
components: {
TypeTitle,
XHeader,
Datetime,
Tab,
TabItem,
Group,
Cell,
XInput,
Timeline,
TimelineItem,
XDialog,
XTextarea,
Loading,
Alert
},
data() {
return {
showValidator: false,
noNetwork: false,
showLoading: false,
remindMsg: "",
taskId: "", // 审批id
procInsId: "", //实例id
status: "", //已审批
approvalDesc: {}, // 审批详情
circuitInfo: [], // 审批流程
showCanceApplyWrap: false,
tabType: "payDesc", //页面导航


newBtnDisabled:false,
showModify: false,
payMentMoney: "",
contract: "",
payMentDate: "",
operator: "",
sysRemark: "",
processList: {
assignee: [],
joint: [],
leader: [],
cC: []
}
};
},
mounted() {
this.getApprovalDesc();
},
methods: {
/**
* 刷新
*/
btnRefresh() {
this.getApprovalDesc();
},

/**
* 跳转到原生选人
*/
_htmlToNativeSelection(selectionName, item) {
let propsData = {
"data": item,
"selection_name": selectionName
}
propsData = JSON.stringify(propsData);
switch (this.$store.state.onLogin.terminal_name) {
case 'ios':
window.HtmlToNativeSelection(propsData);
break;
case 'android':
window.android.HtmlToNativeSelection(propsData);
break;
default:
break;
}
},


/**
* 处理部门审核、会签、领导签发、抄送人员数据 类型：string
*/
getPersonnelList() {
let assigneeList = this.$store.state.processList.assignee,
jointList = this.$store.state.processList.joint,
leaderList = this.$store.state.processList.leader,
cCList = this.$store.state.processList.cC;
this.processList.assignee = this.getPersonnelTypeString(
assigneeList
);
this.processList.joint = this.getPersonnelTypeString(jointList);
this.processList.leader = this.getPersonnelTypeString(leaderList);
this.processList.cC = this.getPersonnelTypeString(cCList);
},

/**
* 获取部门审核、会签、领导签发、抄送人员名单方法
*/
getPersonnelTypeString(personnelList) {
let personnelType = null;
personnelList.forEach(item => {
if (personnelType) {
personnelType += `;${item.name}(${item.num})`;
} else {
personnelType = `${item.name}(${item.num})`;
}
});
return personnelType;
},

/**
* 选择人员
*/
selectPersonnel(url, personnelType) {
let query = {
personnel_type: personnelType
};
this.routeToLink(url, query);
},


operationBtn(btnInfo) {
let approvalDesc = this.approvalDesc;
let _query = {
id: approvalDesc.id,
paymentMoney: approvalDesc.paymentMoney,
paymentDate: approvalDesc.paymentDate,
contractId: approvalDesc.contract.id,
processTask: btnInfo.id,
processTarget: btnInfo.key,
processName: btnInfo.name,
procInsId: this.procInsId
};
console.log('_query:', _query);

switch (btnInfo.key) {
case "submit": //送领导审核
this.submitOpinion(_query);
break;

case "end": //结束流程
this.submitOpinion(_query);
break;

default:
this.routeToLink("/ApprovalComments", _query);
break;
}
},

/**
* 提交审批意见（结束流程、送领导审核、办理完毕按钮）
*/
submitOpinion(query) {
this.getPersonnelList();
this.newBtnDisabled = true;

if (this.formValidator()) {
this.showValidator = true;
this.newBtnDisabled = false;
return;
}
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/cm/purchase/payment/apply";
let propsData = {
id: query.id,
paymentMoney: query.paymentMoney,
paymentDate: query.paymentDate,
contract: {
id: query.contractId
},
processTask: query.processTask,
processTarget: query.processTarget,
processName: query.processName,
procInsId: query.procInsId,
processAssignee: this.processList.assignee,
processJoint: this.processList.joint,
processLeader: this.processList.leader,
cC: this.processList.cC
};
console.log("propsData:", propsData);

return api
.major(url, access_token, propsData)
.then(res => {
console.log("res:", res);

if (res.state == 10001) {
this.remindMsg = res.msg;
this.showPositionValue = true;
this.processComment = "";
this.$router.go(-1);
}
})
.catch(res => {
console.log("出错:", res);
});
},

/**
* 修改验证
*/
formValidator() {
if (
!this.processList.assignee.length ||
!this.processList.joint.length ||
!this.processList.leader.length
) {
return true;
}
},
/**
* 获取审批申请详情
*/
getApprovalDesc() {
this.showLoading = true;
this.noNetwork = false;
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest" + this.$route.query.formKey;
console.log(':', );

this.$route.query.status && (this.status = this.$route.query.status);
console.log("this.$route.name:", this.$route.name);
console.log("this.$route.query:", this.$route.query);

if (this.$route.query.taskName == "起草人修改") {
this.showModify = true;
}

/**
* 判断路径来源：我审批的/我发出的/抄送我的
*/
switch (this.$route.query.source) {
case "myApproval":
this.taskId = this.$route.query.taskId;
break;
case "myInitiated":
this.taskId = this.$route.query.id;

break;
case "carbonCopy":
this.taskId = this.$route.query.taskId;
break;
default:
break;
}
let propsData = {
id: this.taskId,
taskId: this.taskId,
status: this.status
};
console.log("propsData:", propsData);

api.major(url, access_token, propsData)
.then(res => {
console.log("res:", res);
switch (res.state) {
case 10001:
this.approvalDesc = res.data;
this.procInsId = this.approvalDesc.procInsId;
this.getModifyData(this.approvalDesc);
break;

default:
break;
}
this.showLoading = false;
})
.then(res => {
this.getCircuitInfo();
})
.catch(error => {
console.log("error:", error);

this.noNetwork = true;
this.showLoading = false;
});
},

/**
* 获取待修改信息
*/
getModifyData(approvalDesc) {
this.$store.state.payMentMoney = this.payMentMoney = this.approvalDesc.paymentMoney;
this.$store.state.payMentDate = this.payMentDate = this.approvalDesc.paymentDate;
this.$store.state.sysRemark = this.sysRemark = this.approvalDesc.sysRemark;
},

/**
* 获取流程信息
*/
getCircuitInfo() {
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/sys/affair/mobile/comment";
console.log("this.procInsId:", this.procInsId);

let propsData = {
procInsId: this.procInsId
};
api.major(url, access_token, propsData).then(res => {
console.log("res:", res);
this.circuitInfo = res.data.reverse(); 
});
},

/**
* 返回上一页
*/
routeToBack(url, personnelType) {
this.resetModifyData();
this.$router.back();
},

/**
* 重置修改的数据（Vuex）
*/
resetModifyData() {
this.$store.state.processList.assignee = [];
this.$store.state.processList.leader = [];
this.$store.state.processList.joint = [];
this.$store.state.sysRemark = [];
this.$store.state.payMentDate = [];
this.$store.state.payMentMoney = [];
},

/**
* 选人路由
*/
routeToSelection(url, personnelType) {
let _query = {
personnel_type: personnelType
};
this.routeToLink(url, _query);
},

/**
* 路由方法
*/
routeToLink(url, query) {
this.$router.push({
path: url,
query: query && query
});
},

/* 导航切换 start*/
cancelApply() {},
payDesc() {
this.tabType = "payDesc";
},

processInfo() {
this.tabType = "processInfo";
}
/* 导航切换 end*/
}
};
