
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'



import api from "../../lib/api";


import storage from "../../utile/storage";

export default {
components: {
XHeader,
Search,
Group,
Checklist,
Cell,
Divider,
XButton,
CellBox
},
data() {
return {
test: false,
radioName: "name",
checkedAll: false,
currentList: {
often: []
},
processList: {
assignee: [],
leader: []
}
};
},
created() {},
mounted() {
this.getpersonnelList();
},
computed: {},
methods: {
/**
* 返回上一页
*/
routeToBack() {
this.$router.back();
},

changRadio(item, index) {
switch (this.$route.query.personnel_type) {
case "assignee":
this.processList.assignee = [];
this.processList.assignee.push(item);
break;
case "leader":
this.processList.leader = [];
this.processList.leader.push(item);
break;
default:
break;
}
console.log("item:", item);
},


resetProcessList() {
switch (this.$route.query.personnel_type) {
case "assignee":
this.$store.state.processList.assignee = [];
break;
case "leader":
this.$store.state.processList.leader = [];
break;
default:
break;
}
},


submitPersonnelList() {
this.resetProcessList();
this.$store.state.processList.assignee = this.processList.assignee;
this.$store.state.processList.leader = this.processList.leader;
console.log(
"this.$store.state.processList:",
this.$store.state.processList
);

this.$router.back();
},


getpersonnelList() {
let access_token = storage.get("_njLoginInfo").access_token.id;
let url = "rest/sys/perselect/orguser";
let propsData = {};
api.major(url, access_token, propsData).then(res => {
console.log("res:", res);
switch (res.state) {
case 10001:
this.currentList.often = res.data.treeDown;
break;

default:
break;
}
});
}
}
};
