import Vue from 'vue';
import Router from 'vue-router';

// 审批模块
import Approval from './views/approval/Approval.vue'; //审批主页
import MyApproval from './views/approval/MyApproval.vue'; //我审批的
import MyInitiated from './views/approval/MyInitiated.vue'; //我发起的
import CarbonCopy from './views/approval/CarbonCopy.vue'; //抄送我的
import NewApply from './views/approval/NewApply.vue'; //新建
import Contract from './views/approval/Contract.vue'; //合同
import DepartmentReview from './views/approval/DepartmentReview.vue'; //部门审核
import ApprovalComments from './views/approval/ApprovalComments.vue'; //审批意见

//联系人模块
import CheckPersonnel from './views/contactPerson/CheckPersonnel.vue'; //选择人员（多选）
import RadioPersonnel from './views/contactPerson/RadioPersonnel.vue'; //选择人员（单选）
import PersonalDesc from './views/contactPerson/PersonalDesc.vue'; //人员详情


//帮助中心模块
import HelpCenter from './views/helpCenter/HelpCenter.vue'; //帮助中心主页
import Directions from './views/helpCenter/Directions.vue'; //功能说明
import CommonProblem from './views/helpCenter/CommonProblem.vue'; //常见问题
import ProblemDesc from './views/helpCenter/ProblemDesc.vue'; //常见问题详情
import UpdateLog from './views/helpCenter/UpdateLog.vue'; //更新日志
import NewFn from './views/helpCenter/NewFn.vue'; //新功能介绍

//内部咨询模块
import InternalNews from './views/enterpriseNews/internalNews/InternalNews.vue'; //常见问题详情
import AcitveDesc from './views/enterpriseNews/internalNews/AcitveDesc.vue'; //内部资讯列表


//导航模块
import Index from './views/Index.vue'; //导航模块

Vue.use(Router);

export default new Router({
  mode: 'hash',
  base: '/dist/',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/internalNews',
      name: 'internalNews',
      component: InternalNews,
      meta: {
        title: '内部咨询'
      }
    },
    {
      path: '/activeDesc',
      name: 'activeDesc',
      component: AcitveDesc,
      meta: {
        title: '资讯列表'
      }
    },

    /**
     * 帮助中心
     */
    {
      path: '/helpCenter',
      name: 'helpCenter',
      component: HelpCenter,
      meta: {
        title: '帮助中心'
      }
    },
    {
      path: '/directions',
      name: 'directions',
      component: Directions,
      meta: {
        title: '功能说明'
      }
    },
    {
      path: '/commonProblem',
      name: 'commonProblem',
      component: CommonProblem,
      meta: {
        title: '常见问题'
      }
    },
    {
      path: '/problemDesc',
      name: 'problemDesc',
      component: ProblemDesc,
      meta: {
        title: '常见问题详情'
      }
    },
    {
      path: '/updateLog',
      name: 'updateLog',
      component: UpdateLog,
      meta: {
        title: '更新日志'
      }
    },
    {
      path: '/newFn',
      name: 'newFn',
      component: NewFn,
      meta: {
        title: '新功能介绍'
      }
    },

    /**
     * 审批
     */
    {
      path: '/approval',
      name: 'approval',
      component: Approval,
      meta: {
        title: '审批'
      }
    },
    {
      path: '/myApproval',
      name: 'myApproval',
      component: MyApproval,
      meta: {
        title: '我的审批'
      }
    },
    {
      path: '/myInitiated',
      name: 'myInitiated',
      component: MyInitiated,
      meta: {
        title: '我发起的'
      }
    },
    {
      path: '/carbonCopy',
      name: 'carbonCopy',
      component: CarbonCopy,
      meta: {
        title: '抄送我的'
      }
    },
    {
      path: '/newApply',
      name: 'newApply',
      component: NewApply,
      meta: {
        title: '新建采购付款',
        keepAlive: true
      }
    },
    {
      path: '/contract',
      name: 'contract',
      component: Contract,
      meta: {
        title: '选择合同'
      }
    },
    {
      path: '/departmentReview',
      name: 'departmentReview',
      component: DepartmentReview,
      meta: {
        title: '付款申请详情'
      }
    },
    {
      path: '/approvalComments',
      name: 'approvalComments',
      component: ApprovalComments,
      meta: {
        title: '审批意见'
      }
    },

    /**
     * 通讯录
     */
    {
      path: '/personalDesc',
      name: 'personalDesc',
      component: PersonalDesc,
      meta: {
        title: '人员详情'
      }
    },
    {
      path: '/checkPersonnel',
      name: 'checkPersonnel',
      component: CheckPersonnel,
      meta: {
        title: '选择人员(多选)'
      }
    },
    {
      path: '/radioPersonnel',
      name: 'radioPersonnel',
      component: RadioPersonnel,
      meta: {
        title: '选择人员(单选)'
      }
    },
  ],
});
