

import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'



import storage from "../../../utile/storage";


import api from "../../../lib/api";
export default {
components: {
XHeader,
Loading
},
data() {
return {
showLoading: false,
noNetwork: false,
article: {
errorImg: require("../../../assets/images/internalNews/article_default.png"),
currentList: [],
currentPage: 1
}
};
},
created() {
this.getArticleList();
},
mounted() {

},
methods: {
routeToLink(path, query) {
this.$router.push({
path,
query
});
},

getArticleList() {
let url = "rest/cms/InternalNews/article";
let propsData = {
pageNum: this.article.currentPage,
pageSize: 10,
categoryId: this.$route.query.categoryId
};
return this.getArticle(url, propsData);
},

/**
* 获取文章列表方法
*/
getArticle(url, propsData) {
this.showLoading = true;
propsData = propsData || "";
let access_token = storage.get("_njLoginInfo").access_token.id;
return api
.getMajor(url, access_token, propsData)
.then(res => {
console.log("articleRes:", res);
switch (res.state) {
case 10001:
this.article.currentList = res.data;
this.article.currentList.forEach(item => {
item.img =
(item.image &&
"http://dev.cmis.htpc.com.cn:8080/cmis-cms" +
item.image) ||
this.article.errorImg;
console.log("item:", item.img);
});
console.log(
"this.article.currentList:",
this.article.currentList
);
break;
case 10002:
console.log("暂无数据:");

break;
default:
break;
}
this.showLoading = false;
})
.catch(error => {
console.log("error:", error);
this.showLoading = false;
this.noNetwork = true;
});
},
/**
* 下拉刷新
*/
refresh(done) {
this.artice.currentList = [];
this.getArticleList().then(res => {
done();
console.log("下拉刷新:");
});
},

/**
* 上拉加载更多 (暂时取消)
*/
infinite(done) {
console.log("2:", 2);
this.article.currentPage++;
this.getArticleList().then(res => {
done();
console.log("上拉加载");
});
console.log("this.article.currentPage:", this.article.currentPage);
}
}
};
